@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap");

:root {
  --primary-white: #fff;
  --primary-orange: #f9ab00;
  --primary-light-grey: #f4f4f4;
  --primary-grey: #ccd1d9;
  --primary-dark-grey: #666666;
  --primary-dark: rgb(52, 58, 64);
  --primary-dark-nav: #212529;
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
  --primary-black: #000000;
  --primary-green: green;
  --primary-transparent-black: rgba(0, 0, 0, 0.7);
}

body {
  font-family: "Roboto Condensed", sans-serif !important;
  letter-spacing: 0.1rem;
}

/*------- Nav Links ------- */

.navbar {
  z-index: 2;
}
nav ul li {
  text-transform: uppercase;
  cursor: pointer;
}

.navbar-nav > li > a {
  margin: 0 0.75rem;
  color: var(--primary-white) !important;
  border-bottom: 0.1875rem solid var(--primary-dark-nav);
}

.navbar-nav > li > a.active,
.navbar-nav > li > a:focus,
.navbar-nav > li > a:hover {
  border-bottom: 0.1875rem solid var(--primary-orange);
  transition: border-bottom 1s;
}

/*------- Header ------- */

.header-wrapper {
  position: relative;
  background-image: url("./images/wall-and-laptop-background.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100vh;
}

.main-info {
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.main-info > h1 {
  color: var(--primary-orange);
  text-transform: uppercase;
}

.main-info > .typed-text {
  color: var(--primary-white);
  font-size: 2rem;
}

.main-info > .btn-main-resume {
  border: 1px solid var(--primary-red);
  text-transform: uppercase;
  text-decoration: none;
  padding: 0.625rem 1.25rem;
  color: var(--primary-white);
  background-color: var(--primary-red);
  margin-top: 2rem;
  border-radius: 2rem;
  z-index: 2;
}

.main-info > .btn-main-resume:hover {
  background-color: var(--primary-hover-red);
}

@media (max-width: 768px) {
  .main-info > h1 {
    font-size: 1.75rem !important;
  }

  .main-info > .typed-text {
    font-size: 1.5rem !important;
  }

  .main-info > .btn-main-resume {
    font-size: 1rem;
  }
}

/*------- Particles Motion ------- */

.tsparticles-canvas-el {
  position: absolute;
  z-index: 1;
  height: 100vh;
  width: 100vw;
}

@media (max-width: 768px) {
  .tsparticles-canvas-el {
    display: none;
  }
}

/*------- About Me ------- */

.aboutme-container {
  box-shadow: 0px 1px 29px -3px rgba(0, 0, 0, 0.64);
  margin-top: 40px;
}

.photo-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-img {
  width: 30rem;
  border-radius: 50%;
  padding: 0.3rem;
  border: 1px solid var(--primary-orange);
}

.about-header {
  border-bottom: 2px solid var(--primary-dark);
  color: var(--primary-orange);
  text-transform: uppercase;
}

.content {
  text-align: justify;
  padding: 0 5px;
}

@media (max-width: 768px) {
  .profile-img {
    width: 20rem;
  }
  .aboutme-container {
    box-shadow: none;
    margin-top: 0;
  }
}

/*------- Skills ------- */

.skills-wrapper {
  margin-top: 50px !important;
  box-shadow: 0px 1px 29px -3px rgba(0, 0, 0, 0.64);
  position: relative;
  padding: 3.125rem;
}

.skills-header {
  color: var(--primary-orange);
  text-align: center;
  text-transform: uppercase;
}

.skills-content {
  margin: 0 110px !important;
  margin-top: 10px;
}

@media (max-width: 768px) {
  .skills-wrapper {
    box-shadow: none;
    margin-top: 0 !important;
    padding: 1.625rem;
    padding-top: 0;
  }

  .skills-content {
    margin: 0 20px !important;
  }
}
/*------- Experience ------- */

.experience-wrapper {
  box-shadow: 0px 1px 29px -3px rgba(0, 0, 0, 0.64);
  position: relative;
  padding: 3.125rem;
  overflow: hidden;
}

.experience-header {
  margin: 30px 0;
}

.experience h1 {
  text-transform: uppercase;
  color: var(--primary-orange);
}

.experience-wrapper::before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: var(--primary-grey);
  z-index: 1;
}

.experience-block {
  width: calc(50% + 0.5rem);
  display: flex;
  justify-content: space-between;
  clear: both;
}

.experience-block-right {
  float: right;
}

.experience-block-left {
  float: left;
  direction: rtl;
}

.marker {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.3125rem solid var(--primary-dark);
  background: var(--primary-orange);
  margin-top: 0.625rem;
  z-index: 3;
}

.timeline-content {
  width: 95%;
  padding: 0 0.9375rem;
  color: var(--primary-dark-grey);
  direction: ltr;
}

.timeline-content h3 {
  color: var(--primary-red);
  margin-top: 0.3125rem;
  font-weight: 900;
}

.timeline-content h5 {
  color: var(--primary-orange);
  margin-top: 0.3125rem;
  font-weight: 900;
}

.timeline-content ul li {
  list-style-type: square;
}

@media (max-width: 768px) {
  .experience {
    padding-left: 0.7rem;
  }

  .experience-header {
    margin: 0;
  }

  .experience-wrapper {
    box-shadow: none;
  }

  .experience .container {
    padding-left: 0;
  }

  .experience-block {
    width: 100%;
  }

  .experience-wrapper::before {
    left: 0.5rem;
    margin-bottom: 1.875rem;
  }

  .experience-block-right {
    float: none;
  }

  .experience-block-left {
    float: none;
    direction: ltr;
  }
}

/*------- Portfolio ------- */

.portfolio-wrapper {
  background-color: var(--primary-light-grey);
  padding: 3rem 0;
}

.portfolio-wrapper > .container > h1 {
  color: var(--primary-orange);
}

.portfolio-image {
  width: 100% !important;
  /* padding: 0.5rem 0.5rem; */
}

.image-box {
  display: flex !important;
}

.portfolio-image-box {
  position: relative;
}

.overflow {
  position: absolute;
  opacity: 0;
  background-color: var(--primary-black);
  width: 100%;
  height: 100%;
  top: 0;
  overflow: hidden;
}

.portfolio-image-box:hover .overflow {
  opacity: 60%;
  transition: 0.3s ease-in-out;
}

.portfolio-icon-link {
  position: absolute;
  opacity: 0;
  margin-left: -15rem;
  margin-top: 3.5rem;
  color: var(--primary-orange);
  font-size: 1.5rem;
}

.portfolio-image-box:hover .portfolio-icon-link {
  opacity: 70%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

.portfolio-icon-deploy {
  position: absolute;
  opacity: 0;
  margin-left: -5rem;
  margin-top: 3.5rem;
  color: var(--primary-orange);
  font-size: 1.5rem;
}

.portfolio-image-box:hover .portfolio-icon-deploy {
  opacity: 70%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

/*------- Portfolio Card ------- */

.card-container {
  box-shadow: 0px 1px 29px -3px rgba(0, 0, 0, 0.64);
  padding: 0 !important;
  box-sizing: border-box;
  margin-bottom: 40px;
}

.card-row .card-container:nth-of-type(2),
.card-row .card-container:nth-of-type(5) {
  margin-left: 142px !important;
  margin-right: 142px !important;
  /* margin-bottom: 40px; */
}

.card-container h3 {
  color: var(--primary-orange);
}

.card-content {
  padding: 12px;
}

.card-content p {
  text-align: justify;
}

@media (max-width: 768px) {
  .portfolio-wrapper {
    background-color: var(--primary-light-grey);
    padding: 0;
  }

  .card-container {
    margin: 0;
    margin-bottom: 40px;
  }
}
/*------- Contact Component ------- */

.contacts {
  background-color: var(--primary-dark);
  padding: 3rem 0;
}

.contacts h1 {
  color: var(--primary-orange);
  padding: 1.3rem 0;
  text-transform: uppercase;
}

.contacts p {
  color: var(--primary-light-grey);
  font-size: 1.2rem;
}

.btn-main-submit {
  border: 2px solid var(--primary-red);
  color: white;
  padding: 0.313rem 0.938rem;
  border-radius: 5px;
  letter-spacing: 0.1rem;
  width: 100%;
  margin: 1rem 0 0 0;
  background: none;
}

.btn-main-submit:hover {
  background-color: var(--primary-hover-red);
}

.contacts input,
.contacts textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
  border-radius: 0;
  color: var(--primary-orange);
  box-shadow: none !important;
  outline: 0 !important;
  margin-bottom: 1rem;
}

.contacts .container {
  padding: 4rem 0;
}

.contacts input:focus,
.contacts textarea:focus {
  background: transparent;
  color: var(--primary-orange);
  border-bottom: 0.125rem solid var(--primary-orange);
  transition: border-bottom 1s;
}

.contacts textarea {
  height: 12.5rem;
}

.contact-btn {
  width: 100%;
  margin: 1rem 0 0 0;
  background: none;
}

.error-message {
  color: var(--primary-white);
  background-color: var(--primary-red);
}

.success-message {
  font-weight: 900;
  color: var(--primary-green);
}

@media (max-width: 768px) {
  .contacts {
    overflow-x: hidden;
  }

  .contacts .container {
    padding: 0 1rem;
  }
}

/*------- Footer Component ------- */

.footer {
  background: var(--primary-black);
  color: var(--primary-dark-grey);
  padding: 3rem;
  font-weight: 900 !important;
}

.footer a {
  text-decoration: none;
  color: var(--primary-dark-grey);
}

.footer a:hover {
  text-decoration: none;
  color: var(--primary-orange);
  cursor: pointer;
}

.footer p {
  margin-bottom: 0;
}

.social-media > a {
  margin-right: 15px;
}

.social-media > a > .linkedin {
  height: 30px;
  width: 30px;
}

.social-media > a > .github {
  color: var(--primary-white);
}

@media (max-width: 768px) {
  .social-media {
    margin-top: 20px;
  }
}
